.about {
    font-family: 'Roboto Flex';
    margin-top: 120px;
    background-image: url("../../images/about.png");
    background-position: top;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: row-reverse;
}

.about__text {
    width: 674px;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
}

.about__snake {
    margin: 0 0 120px 96px;
}

.about__snake_container {
    width: 741px;
    height: 1056px;
}

.about__snake_text {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    margin: 16px 0 0 0;
}

.about__lion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 120px 96px 0;
}

.lion__description {
    display: flex;
    flex-direction: column;
}

.lion__title {
    font-size: 96px;
    font-style: normal;
    font-weight: 900;
    line-height: 104px;
    margin: 0 0 56px 0;
}

.lion__text {
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 32px;
    width: 734px;
}

.lion__image {
    width: 576px;
    height: 770px;
}

.lion__image_text {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    margin: 16px 0 0 0;
}

.exhibition__title {
    font-size: 80px;
    font-style: normal;
    font-weight: 500;
    line-height: 104px;
    margin: 0;

}

.table {
    margin-top: 128px;
}

.table__top {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.table__arrow {
    margin-left: 72px;
}

.table__title {
    font-size: 28px;
    font-style: normal;
    font-weight: 900;
    line-height: 34px;
    text-decoration: none;
    color: white;
    display: flex;
    flex-direction: row;
    width: 610px;
}

.table__line_top {
    width: 1px;
    height: 68px;
    background: #FFF;
}


.table__bot {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.table__line_bot {
    width: 1px;
    height: 404px;
    background: #FFF;
    margin: 16px 0;
}

.table__text {
    width: 610px;
}

.table__bot_title {
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 2.4px
}

.table__bot_text {
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 38px;
    letter-spacing: 1.92px;
}